//third party libraries
import { Router } from 'express'
const playerRoutes = Router()

// Internal code/packages
import authCheck from '../lib/authCheck'
import PlayerController from '../controllers/playerController'

export default (db, logger) => {
  const playerController = new ProfileController(db, logger)

  /**
   * @swagger
   * definitions:
   *   Player:
   *     properties:
   *       created:
   *         type: "string"
   *       createdAsUid:
   *         type: "string"
   *       gender:
   *         type: "string"
   *       image:
   *         type: "string"
   *       name:
   *         type: "string"
   *       race:
   *         type: string"
   *       passing:
   *         type: "number"
   *       blocking:
   *         type: "number"
   *       throwing:
   *         type: "number"
   *       tougness:
   *         type: "number"
   *       vision:
   *         type: "number"
   *       endurance:
   *         type: "number"
   *       leadership:
   *         type: "number"
   *       morale:
   *         type: "number"
   *       fatigue:
   *         type: "number"
   *       aggression:
   *         type: "number"
   */

  /**
   * @swagger
   * /players:
   *   x-swagger-router-controller: ../controllers/playerController
   *   get:
   *     tags:
   *       - Players
   *     description: Get a list of players
   *     operationId: list
   *     produces:
   *       - application/json
   *     responses:
   *       200:
   *         description: Success
   *         schema:
   *           $ref: "#/definitions/Player"
   *       400:
   *         description: Bad request
   */
  playerRoutes.get('/', authCheck, (req, res) => {
    playerController.list(req, res)
  })

/**
 * @swagger
 * /players/{id}:
 *   x-swagger-router-controller: ../controllers/playersController
 *   get:
 *     tags:
 *       - Players
 *     description: Get a single player by uid
 *     operationId: listOne
 *     produces:
 *       - application/json
 *     parameters:
 *       - uid: player uid
 *         description: UID of the player
 *         in: path
 *         name: uid
 *         required: true
 *         schema:
 *           $ref: '#/definitions/Player'
 *     responses:
 *       200:
 *         description: Success
 *         schema:
 *           $ref: "#/definitions/Player"
 */
  playerRoutes.get('/:id', authCheck, (req, res) => {
    playerController.listOne(req, res)
  })

  return playerRoutes
}